@import "../../css/seidenader-foundation.scss";

.nav {
  z-index: 2;
  padding: 1.5rem 0;
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  display: flex;
  justify-content: space-between;
  .flex-container {
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }
}

.navbar {
  background: $grey243;
  z-index: 5;
}

.navbar > div {
  flex-basis: 100%;
  margin: 0.5rem 0.5rem;
  text-align: center;
}

.nav-hidden {
  margin-top: -7rem;
}

.hider {
  position: fixed;
  font-size: $fxs;
  top: 0;
  cursor: pointer;
  left: 48%;
  background: white;
  z-index: 999;
  padding: 0.5rem;
  padding-bottom: 1rem;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  margin-top: -2rem;
  transition: all 0.5s;
}

.hider:hover {
  margin-top: 0rem;
}

.config-wrapper {
  position: relative;
  border: none;
  display: flex;
  align-items: center;
  padding: 1rem;
  color: $black;
  font-size: $fxs;
  text-align: left;
  padding: 1.5rem;
  color: $grey62;
  height: 5rem;
  img {
    margin-right: 0.5rem;
  }
}

.separator {
  border-left: 0.1rem solid $grey204;
}