@import "../../css/seidenader-foundation.scss";

.action-bar-wrapper {
  display: flex;
  bottom: 0;
  left: 0;
  height: 3rem;
  width: 100%;
  align-items: center;
  background: $grey243;
}

.action-bar-separator {
  width: 20px;
  height: 100%;
}

.action-bar-separator > div {
  height: calc(100% - 10px);
    margin-left: auto;
    margin-right: auto;
    width: 1px;
    margin-top: 5px;
    margin-bottom: 5px;
}