@import "../../../css/seidenader-foundation.scss";

.toggle-wrapper {
  max-width: 30rem;
  height: 5rem;
  display: flex;
  align-items: center;
  padding: 1rem;
  cursor: pointer;
  &:hover {
    background: $grey204;
  }
}

.toggle-wrapper div {
  padding-top: 5px;
}

.toggle-wrapper p {
  padding-top: 0.5rem;
  padding-left: 0.5rem;
  white-space: nowrap;
  line-height: 1.7rem;
  text-overflow: ellipsis;
  overflow: hidden;
}

.toggle-text {
  font-size: $fxs;
}