@import '../../css/variables.scss';

.requests-list {
  height: 100%;
  min-width: 300px;
  background-color: $grey237;
  display: flex;
  flex-direction: column;

  .request-list-header {
    font-size: 16pt;
    background-color: $grey204;
    padding: 10px 5px;
    display: flex;
    flex-direction: column;
    flex: 0 0 50px;
  
    > small {
      color: $grey115;
      font-style: italic;
      font-size: 12pt;
    }
  }

  .requests-filter {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 5px;
    flex: 0 0 50px;

    background-color: $grey237;
    :not(:last-child) {
      margin-right: 4px;
    }
  }

  .requests {
    flex: 1 0 50px;
    overflow-y: auto;
    ul {
      li {
        display: block;
        padding: 10px 5px;
        cursor: pointer;

        &:nth-child(odd) {
          background-color: $grey243;
        }
        &:nth-child(even) {
          background-color: $grey237;
        }

        &.active {
          background-color: $grey204;
          font-weight: bolder;
        }
      }
    }
  }
}
