@import "../../css/react-mosaic.css";

.view-set-wrapper {
  height: 100%;
}
.bottom-nav-offset {
  padding-bottom: 5rem;
}

.top-nav-hidden {
  padding-top: 7rem;
}

.mosaic-default-control {
  height: 18px;
  width: 18px;
  margin: 0 2px 0 2px;
}

.expand-button {
  background: url(../../assets/icons/settings-overscan.svg);
  background-position: center center;
  background-size: 100% 100%;
}

.split-button {
  background: url(../../assets/icons/plus.svg);
  background-position: center center;
  background-size: 100% 100%;
}

.close-button {
  background: url(../../assets/icons/close.svg);
  background-position: center center;
  background-size: 100% 100%;
}

.mosaic-window-title {
  font-family: Calibri, sans-serif !important;
}