@import "../../css/seidenader-foundation.scss";

.image-view-wrapper {
  height: 100%;
  width: 100%;
  padding-bottom: 3rem;
  overflow: auto;
  .info-box {
    border: 0.1rem solid $grey237;
    position: absolute;
    right: 0;
    top: 0;
    width: 20rem;
    height: calc(100% - 3rem);
    background: $white;

    .info-table {
      display: flex;
      flex-flow: column;
      font-size: $fxs;

      .th {
        display: flex;
        padding: 0.5rem;

        .td {
          flex-basis: 100%;
          text-align: center;
        }
      }

      .tr {
        display: flex;
        flex-basis: 100%;
        flex-flow: row;

        .td {
          outline: 0.1rem solid $grey243;
          flex-basis: 50%;
          text-align: left;
          padding: 0.5rem;
        }
      }
    }
  }

  .layer {
    position: absolute;
    left: 0;
    top: 0;
  }
}

.checkbox-wrapper {
  padding: 0.2rem;
}
