@import "../../css/seidenader-foundation.scss";

.dialog {
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  position: fixed;
  display: flex;
  z-index: 99;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  align-items: center;
  justify-content: center;
  overflow: hidden;

  .dialog-wrapper {
    position: absolute;
    min-width: 25%;
    background: $white;
    align-items: center;
    justify-content: center;
    border: 0.1rem solid $grey115;
    @include box-shadow();

    .dialog-toolbar {
      display: flex;
      height: 3rem;
      user-select: none;
      justify-content: space-between;
      align-items: center;
      border-bottom: 0.1rem solid $grey237;
      .dialog-title {
        flex: 1;
        padding-left: 1.5rem;
        text-overflow: ellipsis;
        overflow: hidden;
        min-height: 1.8rem;
        font-size: $fxs;
      }
      .dialog-controll-wrapper {
        line-height: $fxs;
      }
    }

    .dialog-content-wrapper {
      height: 100%;
      padding-left: 1rem;
      padding-right: 1rem;
      margin-bottom: 3rem;
    }
  }
}

.is-in-view {
  position: absolute;
  align-items: flex-start;
  height: 100%;
}

.is-width-100 {
  width: 100%
}

.is-height-100 {
  height: 100%
}

.is-padding-0 {
  padding: 0 !important;
}

.isPopup {
  width: calc(100% - 3rem) !important;
  height: calc(100% - 3rem) !important;
  top: 1.5rem;
}
