.modal-fullscreen-style {
  position: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  background-color: rgba(100, 100, 100, 0.5);
  z-index: 1600;
  text-align: center;
  color: white;
  font-size: 3.5em;
}

.modal-fullscreen-inner {
  margin-left: auto;
  margin-right: auto;
}
