@import "../../../../../../css/seidenader-foundation.scss";

.preset-button {
  padding: 2rem 2rem;
  font-size: $fxs;
  color: $grey62;
  cursor: pointer;

  &:hover {
    background: $grey204;
  }

  span {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    max-width: 10rem;
    display: inline-block;
  }
}

.preset-button-active {
  background: $grey204;
}
