@import "./variables.scss";
html {
  font-size: 10px;
  font-family: $font-family;
}

a {
  text-decoration: none;
}

button,
input,
div {
  appearance: none;
  outline: none;
  border: none;
  border-radius: 0;
  background: transparent;
  margin: 0;
  padding: 0;
}

button::-moz-focus-inner {
  border: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
