@import "../../../../css/seidenader-foundation.scss";

.error {
  color: red;
}

.status-bar {
  max-width: 30rem;
  height: 5rem;
  display: flex;
  align-items: center;
  padding: 1rem;
  cursor: pointer;
  &:hover {
    background: $grey204;
  }
}

.status-bar p {
  padding-top: 0.5rem;
  padding-left: 0.5rem;
  white-space: nowrap;
  line-height: 1.7rem;
  text-overflow: ellipsis;
  overflow: hidden;
}

.error:before {
  color: $red;
  font-size: 1.2rem;
  margin-right: 0.5rem;
}

.info {
  font-size: $fxs;
}

.status-list {
  min-width: 30rem;
  height: 50rem;
  width: 42rem;
  margin-top: 1.5rem;
  position: absolute;
  background: $grey237;
  text-align: left;
  padding: 1rem;
  @include box-shadow();
}

.status-container {
  overflow: auto;
  height: 100%;
  .status {
    margin: 0;
    padding: 1rem;
  }
  .status:not(:last-child) {
    border-bottom: 0.1rem solid $grey204;
  }
}
