@import "../../css/seidenader-foundation.scss";

.permission-management-wrapper {
  margin-top: 55px;
  margin-bottom: 10px;

  button {
    margin-left: 20px;
    margin-top: 20px;
  }

  h1, h3 {
    padding-left: 20px;
  }

  flex-direction: column;

  .ReactTable {
    position: relative;
    display: flex;
    flex-direction: column;
    font-size: $fxxs;
    width: 1000px;
    * {
      box-sizing: border-box;
    }
    .rt-table {
      flex: auto 1;
      display: flex;
      flex-direction: column;
      align-items: stretch;
      width: 100%;
      border-collapse: collapse;
      overflow: auto;
      .rt-tr {
        flex: 1 0 auto;
        display: inline-flex;
        height: 25px;
      }
      .rt-th,
      .rt-td {
        flex: 1 0 0;
        white-space: nowrap;
        text-overflow: ellipsis;
        padding: 2px 2px;
        overflow: hidden;
        height: 100%;
      }
    }
  }
}

.permission-management-table-wrapper {
  border: 1px solid $grey204;
  margin-left: 20px;
}

.permission-management-permissions-header {
  font-size: 1.4rem;
  text-align: center;
  font-weight: 600;
}

.permission-management-permissions-subheader {
  font-size: 1rem;
  text-align: center;
  min-height: 1.2rem;
}

.permission-management-permissions-header-left {
  font-size: 1.4rem;
  padding-left: 10px;
  font-weight: 600;
  text-align: left;
}

.permission-management-label {
  width: 100%;
  margin-left: 10px;
  padding: 3px 0;
}

.permission-management-checkbox {
  height: 20px;
  padding: 0 !important;
  width: 100%;
  height: 100%;
}

.permission-management-fixed-header {
  position: fixed;
  top: 0;
  background-color: #f3f3f3;
  z-index: 5;
  width: 100%;
  padding-left: 15px;
  border-bottom: 1px solid #cccccc;

  button {
    margin: 5px;
  }
}