@import '../../css/variables.scss';

.request-parameters-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding-bottom: 5px;

  .request-name-type {
    background-color: $grey204;
    font-size: 16pt;
    padding: 10px 5px;
    display: flex;
    flex-direction: column;
    flex: 1 2 auto;

    > small {
      color: $grey115;
      font-style: italic;
      font-size: 12pt;
    }
  }

  .request-parameters-fields-container {
    padding: 15px;
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid $grey237;
    margin-bottom: 5px;

    width: 100%;
    height: 100%;
    overflow-y: auto;

    .field-container {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      min-width: 100%;

      &:not(:last-child) {
        border-bottom: 1px solid $grey204;
        padding-bottom: 3px;
        margin-bottom: 3px;
      }

      &.is-array {
        flex-direction: column;
        border-bottom: 0;
      }

      .field-name-type {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        flex: 0 0 250px;

        > small {
          color: $grey115;
          font-style: italic;
        }
      }

      .field-settings {
        flex: 5 0 150px;
      }
    }
  }

  button {
    margin: 15px 5px;
  }
}
