@import '../../css/seidenader-foundation.scss';

.list-view-wrapper {
  height: 100%;
  width: 100%;
  padding-bottom: 3rem;
  overflow: auto;

  .ReactTable {
    position: relative;
    display: flex;
    flex-direction: column;
    font-size: $fxxs;
    * {
      box-sizing: border-box;
    }
    .rt-table {
      flex: auto 1;
      display: flex;
      flex-direction: column;
      align-items: stretch;
      width: 100%;
      border-collapse: collapse;
      overflow: auto;
    }
    .rt-thead {
      flex: 1 0 auto;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      flex-direction: column;
      user-select: none;
      &.-headerGroups {
        background: #fff;
      }
      &.-filters {
        border-bottom: 1px solid #fff;
        input,
        select {
          border: 1px solid rgba(0, 0, 0, 0.1);
          background: #fff;
          padding: 5px 7px;
          font-size: inherit;
          border-radius: 3px;
          font-weight: normal;
          outline: none;
        }
        .rt-th {
          border-right: 1px solid rgba(0, 0, 0, 0.02);
        }
      }
      &.-header {
        border-bottom: 1px solid $grey204;
      }
      .rt-tr {
        text-align: center;
      }
      .rt-th,
      .rt-td {
        padding: 5px 5px;
        line-height: normal;
        position: relative;
        border-right: 1px solid rgba(0, 0, 0, 0.05);
        transition: box-shadow 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
        box-shadow: inset 0 0 0 0 transparent;
      }
      .rt-th.-sort-asc,
      .rt-td.-sort-asc {
        box-shadow: inset 0 3px 0 0 rgba(0, 0, 0, 0.6);
      }
      .rt-th.-sort-desc,
      .rt-td.-sort-desc {
        box-shadow: inset 0 -3px 0 0 rgba(0, 0, 0, 0.6);
      }
      .rt-th.-cursor-pointer,
      .rt-td.-cursor-pointer {
        cursor: pointer;
      }
      .rt-th:last-child,
      .rt-td:last-child {
        border-right: 0;
      }
      .rt-resizable-header {
        overflow: visible;
        display: flex;
        align-items: center;
        justify-content: center;
        &:last-child {
          overflow: hidden;
        }
      }
      .rt-resizable-header-content {
        overflow: hidden;
        text-overflow: ellipsis;
      }
      .rt-header-pivot {
        border-right-color: #f7f7f7;
        &:after,
        &:before {
          left: 100%;
          top: 50%;
          border: solid transparent;
          content: ' ';
          height: 0;
          width: 0;
          position: absolute;
          pointer-events: none;
        }
        &:after {
          border-color: rgba(255, 255, 255, 0);
          border-left-color: #fff;
          border-width: 8px;
          margin-top: -8px;
        }
        &:before {
          border-color: rgba(102, 102, 102, 0);
          border-left-color: #f7f7f7;
          border-width: 10px;
          margin-top: -10px;
        }
      }
    }
    .rt-tbody {
      flex: 99999 1 auto;
      display: flex;
      flex-direction: column;
      overflow: auto;
      .rt-tr-group {
        border-bottom: solid 1px rgba(0, 0, 0, 0.05);
        &:last-child {
          border-bottom: 0;
        }
      }
      .rt-td {
        border-right: 1px solid rgba(0, 0, 0, 0.02);
        &:last-child {
          border-right: 0;
        }
      }
      .rt-expandable {
        cursor: pointer;
        text-overflow: clip;
      }
    }
    .rt-tr-group {
      flex: 1 0 auto;
      display: flex;
      flex-direction: column;
      align-items: stretch;
    }
    .rt-tr {
      flex: 1 0 auto;
      display: inline-flex;
    }
    .rt-th,
    .rt-td {
      flex: 1 0 0;
      white-space: nowrap;
      text-overflow: ellipsis;
      padding: 7px 5px;
      overflow: hidden;
      transition: 0.3s ease;
      transition-property: width, min-width, padding, opacity;
    }
    .rt-th.-hidden,
    .rt-td.-hidden {
      width: 0 !important;
      min-width: 0 !important;
      padding: 0 !important;
      border: 0 !important;
      opacity: 0 !important;
    }
    .rt-expander {
      display: inline-block;
      position: relative;
      margin: 0;
      color: transparent;
      margin: 0 10px;
      &:after {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(-90deg);
        border-left: 5.04px solid transparent;
        border-right: 5.04px solid transparent;
        border-top: 7px solid rgba(0, 0, 0, 0.8);
        transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
        cursor: pointer;
      }
      &.-open:after {
        transform: translate(-50%, -50%) rotate(0);
      }
    }
    .rt-resizer {
      display: inline-block;
      position: absolute;
      width: 36px;
      top: 0;
      bottom: 0;
      right: -18px;
      cursor: col-resize;
      z-index: 10;
    }
    .rt-tfoot {
      flex: 1 0 auto;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      flex-direction: column;
      box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.15);
      .rt-td {
        border-right: 1px solid rgba(0, 0, 0, 0.05);
        &:last-child {
          border-right: 0;
        }
      }
    }
    &.-striped .rt-tr.-odd {
      background: rgba(0, 0, 0, 0.03);
    }
    &.-highlight .rt-tbody .rt-tr:not(.-padRow):hover {
      background: rgba(0, 0, 0, 0.05);
    }
    .-pagination {
      z-index: 1;
      display: flex;
      justify-content: space-between;
      align-items: stretch;
      flex-wrap: wrap;
      padding: 3px;
      box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
      border-top: 2px solid rgba(0, 0, 0, 0.1);
      input,
      select {
        border: 1px solid rgba(0, 0, 0, 0.1);
        background: #fff;
        padding: 5px 7px;
        font-size: inherit;
        border-radius: 3px;
        font-weight: normal;
        outline: none;
      }
      .-btn {
        appearance: none;
        display: block;
        width: 100%;
        height: 100%;
        border: 0;
        border-radius: 3px;
        padding: 6px;
        font-size: 1em;
        color: rgba(0, 0, 0, 0.6);
        background: rgba(0, 0, 0, 0.1);
        transition: all 0.1s ease;
        cursor: pointer;
        outline: none;
        &[disabled] {
          opacity: 0.5;
          cursor: default;
        }
        &:not([disabled]):hover {
          background: rgba(0, 0, 0, 0.3);
          color: #fff;
        }
      }
      .-previous,
      .-next {
        flex: 1;
        text-align: center;
      }
      .-center {
        flex: 1.5;
        text-align: center;
        margin-bottom: 0;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-around;
      }
      .-pageInfo {
        display: inline-block;
        margin: 3px 10px;
        white-space: nowrap;
      }
      .-pageJump {
        display: inline-block;
        input {
          width: 70px;
          text-align: center;
        }
      }
      .-pageSizeOptions {
        margin: 3px 10px;
      }
    }
    .rt-noData {
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      background: rgba(255, 255, 255, 0.8);
      transition: all 0.3s ease;
      z-index: 1;
      pointer-events: none;
      padding: 20px;
      color: rgba(0, 0, 0, 0.5);
    }
    .-loading {
      display: block;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      background: rgba(255, 255, 255, 0.8);
      transition: all 0.3s ease;
      z-index: -1;
      opacity: 0;
      pointer-events: none;
      > div {
        position: absolute;
        display: block;
        text-align: center;
        width: 100%;
        top: 50%;
        left: 0;
        font-size: $fxxs;
        color: rgba(0, 0, 0, 0.6);
        transform: translateY(-52%);
        transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
      }
      &.-active {
        opacity: 1;
        z-index: 2;
        pointer-events: all;
        > div {
          transform: translateY(50%);
        }
      }
    }
    .rt-resizing {
      .rt-th,
      .rt-td {
        transition: none !important;
        cursor: col-resize;
        user-select: none;
      }
    }
    [type='checkbox'] {
      padding: 0.5rem;
      border: 1px solid black;
    }
    [type='checkbox']:checked {
      background-color: black;
    }
  }
}
