.reject-view-container {
  width: 100%;
  height: 100%;
  .film-strip-container {
    overflow-x: auto;
    white-space: nowrap;
    .reject-image {
      display: inline-block;
      align-items: center;
      cursor: pointer;
      width: 15rem;
      height: auto;
      margin-right: 0.3rem;
      background-size: 100%;
      background-position: center;
      background-repeat: no-repeat;
      
      img {
        width: 100%;
        height: auto;
      }
    }
  }
}
