* {
  font-family: "Calibri", sans-serif;
}

.basis-100 {
  flex-basis: 100%;
}

.basis-50 {
  flex-basis: 50%;
}

.basis-10 {
  flex-basis: 10%;
}

.fa7 {
  font-size: 0.6rem;
}

.left-100 {
  left: 100%;
}

.fullscreen {
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: fixed !important;
  z-index: 99;
}

.editing-focus {
  outline: 1px solid #7fff7f
}

.pt-70 {
  padding-top: 70px;
}

.transition-all {
  transition: all 0.8s;
}

.disabled {
  pointer-events: none;
  opacity: 0.4;
}

.flex-wrapper {
  display: flex;
  align-items: center;
}
