@import "../../../../css/seidenader-foundation.scss";

.toolset-bar-wrapper {
  display: flex;
  flex-direction: column;
  top: 0;
  left: 0;
  height: 100%;
  width: 3rem;
  background: $grey243;
}

.toolset-bar-separator {
  height: 20px;
  width: 100%;
}

.toolset-bar-separator > div {
  width: calc(100% - 10px);
  margin-left: 5px;
  margin-right: 5px;
  height: 1px;
  margin-top: auto;
  margin-bottom: auto;
}