@import '../../../../css/seidenader-foundation.scss';

.list-item,
.list-header {
  display: flex;
  font-size: 1.6rem;
  padding: 0.5rem 1.5rem;
  margin-bottom: 0.5rem;

  &:nth-child(2n + 2) {
    background: $grey243;
  }

  span {
    flex-basis: 100%;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  span:first-child {
    flex-basis: 10%;
  }

  span:nth-child(2) {
    flex-basis: 70%;
  }

  span:nth-child(3) {
    flex-basis: 20%;
  }

  span:nth-child(4) {
    flex-basis: 10%;
  }

  .list-number {
    text-align: left;
    flex-basis: 20rem;
  }

  .list-action {
    flex-basis: 20rem;

    button {
      background: $grey204;
      padding: 0 0.5rem;
      color: $white;
    }
  }
}

.list-header {
  position: sticky;
  top: -0.5rem;
  z-index: 1;
  background-color: $white;
  span {
    font-weight: 100;
    color: $grey204;
    text-align: left;
  }
}

.alias-editor-wrapper {
  display: table;
  width: 100%;
}

.row {
  display: table-row;
  width: 100%;
}

.label {
  display: table-cell;
  width: 100px;
  font-size: $fxxs;
  vertical-align: middle;
  padding-top: 5px;
  padding-left: 10px;
}

.textfield {
  display: table-cell;
  padding-top: 5px;
}

.buttons {
  position: absolute;
  bottom: 5px;
  right: 5px;
}
