// Fonts
$font-family: "Calibri", sans-serif;

// Font sizes
$fxxs: 1.2rem;
$fxs: 1.4rem;
$fs: 1.6rem;
$fm: 1.8rem;
$fl: 2.1rem;
$fxl: 2.4rem;
$fxxl: 3.1rem;

// Colors
$white: rgb(255, 255, 255);
$whiteOpacity60: rgba(255, 255, 255, 0.6);
$black: rgb(0, 0, 0);
$orange: rgb(220, 122, 0);
$red: rgb(193, 37, 37);
$green: rgb(127, 196, 0);
$blue: rgb(0, 123, 170);
$grey62: rgb(51, 51, 51);
$grey62: rgb(62, 62, 62);
$grey77: rgb(77, 77, 77);
$grey115: rgb(115, 115, 115);
$grey128: rgb(128, 128, 128);
$grey135: rgb(135, 135, 135);
$grey115Opacity30: rgba(115, 115, 115, 0.3);
$grey204: rgb(204, 204, 204);
$grey237: rgb(237, 237, 237);
$grey243: rgb(243, 243, 243);

@mixin box-shadow() {
  box-shadow: 0rem 0rem 0.5rem 0rem rgba(0, 0, 0, 0.5);
}
