@import '../../../../css/seidenader-foundation.scss';

.lock-state-handler {
  position: relative;
  border: none;
  font-size: $fxs;
  display: flex;
  align-items: center;
}

.lock-config-button {
  max-width: 30rem;
  height: 5rem;
  display: flex;
  align-items: center;
  padding: 1rem;
  cursor: pointer;
  &:hover {
    background: $grey204;
  }
}

.lock-config-button p {
  padding-top: 0.5rem;
  padding-left: 0.5rem;
  white-space: nowrap;
  line-height: 1.7rem;
  text-overflow: ellipsis;
  overflow: hidden;
}

.lock-status {
  height: 5rem;
  padding: 0.2rem;
  text-overflow: ellipsis;
  overflow: hidden;
}

.lock-status p {
  margin: 0%;
}

.auto-accept-container {
  text-align: center;
}

.auto-accept-container p {
  font-size: 14px;
}

.config-lock-status {
  width: 100%;
}
