@import "../../../../css/seidenader-foundation.scss";

.parameter-view-filter-wrapper {
  display: flex;
  align-items: stretch;
  height: 100%;

  .filter-list {
    min-width: 15rem;
    flex-grow: 2;
    border: 0.1rem solid $grey237;
    height: 100%;
    display: flex;
    flex-direction: column;
    margin-right: 5rem;
    .filter-header {
      font-size: $fxs;
      border-bottom: 0.1rem solid $grey237;
      background-color: $white;
      padding: 0.5rem;
      text-align: center;
    }

    .filter-content {
      flex-grow: 1;
      overflow-y: auto;
      .filter-list-row {
        display: flex;
        font-size: $fxs;
        padding: 0.5rem 1rem;
        cursor: pointer;

        &:nth-child(2n + 2) {
          background: $grey243;
        }

        .filter-list-row-item {
          flex-basis: 100%;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          user-select: none;
        }
      }
    }

    .filter-actions {
    }
  }

  .property-container {
    .filter-header {
      font-size: $fxs;
      border-bottom: 0.1rem solid $grey237;
      background-color: $white;
      padding: 0.5rem;
      text-align: center;
    }
    border: 0.1rem solid $grey237;
    min-width: 35rem;
    flex-grow: 8;
    overflow: hidden;
  }
}

.filter-list-row-item-on-drag {
  z-index: 10;
  padding: 0.5rem 1rem;
  list-style: none;
  font-size: $fxs;
  opacity: 0.5;
}
