@import '../../css/variables.scss';

.response-body-container {
  height: 100%;
  display: flex;
  flex-direction: column;

  .response-body-header {
    display: flex;
    background-color: $grey204;
    padding: 10px 5px;
    .response-name-type {
      font-size: 16pt;

      display: flex;
      flex-direction: column;
      flex: 1 2 auto;

      > small {
        color: $grey115;
        font-style: italic;
        font-size: 12pt;
      }
    }
  }

  .response-content {
    padding: 15px;
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid $grey237;

    width: 100%;
    height: 100%;
    overflow-y: auto;

    .response-formatter {
      white-space: pre-wrap;
      margin-top: 0;
      padding-bottom: 15px;
    }
  }
}
