@import "../../css/seidenader-foundation.scss";

.bottom-nav-wrapper {
  position: fixed;
  z-index: 1;
  left: 0;
  bottom: 0;
  width: 100%;
  .bottom-nav {
    background-color: $grey243;
    display: flex;
  }
  .bottom-hider {
    bottom: 0rem;
    position: fixed;
    cursor: pointer;
    left: 48%;
    background: white;
    z-index: 999;
    padding: 0.5rem;
    padding-top: 1rem;
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
    margin-bottom: -2.2rem;
    transition: all 0.5s;
    font-size: $fxs;
    &:hover {
      margin-bottom: 0;
    }
  }
}
.bottom-nav-hidden {
  margin-bottom: -10rem;
}

.view-manager {
  align-self: center;
  margin-left: auto;
  font-size: $fxs;
  display: flex;
  align-items: center;
  height: 5rem;
  padding: 1.5rem;
  cursor: pointer;
  img {
    max-width: 2rem;
    margin-right: 1rem;
  }
  &:hover {
    background: $grey204;
    color: $black;
  }
}
