@import "../../../../css/seidenader-foundation.scss";

.parameter-view-overlay-wrapper {
  display: flex;
  align-items: stretch;
  height: 100%;
}

.objectselector-in-dialog-wrapper {
  height: 100%;
  overflow: auto;
}

.icon-spacer {
  flex-basis: 100%;
}

.edit-overlay-div {
  width: 100%;
  height: 100%;
  font-size: 14pt;
}

.ReactTable {
  .rt-th,
  .rt-td {
    padding: 2px 2px;
    line-height: normal;
    position: relative;
    border-right: 1px solid rgba(0, 0, 0, 0.05);
    transition: box-shadow 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    box-shadow: inset 0 0 0 0 transparent;
    height: 30px;
  }
}


.parameter-view-overlay-table-wrapper {
  width: 30%;
}

.parameter-view-overlay-table-wrapper .action-bar-wrapper {
  position: absolute !important;
}

.parameter-view-overlay-overlaypropertylist {
  overflow-y: scroll;
  padding: 10px;
  border: 1px solid $grey243;
  width: 70%;
}

.parameter-view-overlay-overlaypropertylist>div {
  margin-top: 10px;
}

.propertylist-header {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.parameter-view-overlay-placeholder {
  margin-top: 20px;
  font-size: 2rem;
  text-align: center;
}