@import '../../css/seidenader-foundation.scss';

.debugger-wrapper {
  font-size: 16px;

  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-template-rows: 0.5fr repeat(2, minmax(30vh, 5fr));
  width: 100%;

  h1,
  h3 {
    padding-left: 10px;
  }

  .debugger-header {
    grid-column: 1/3;
    grid-row: 1;
    height: 50px;
    background-color: #f3f3f3;
    z-index: 5;
    width: 100%;
    border-bottom: 1px solid #cccccc;

    a {
      margin: 5px;
    }
  }

  .request-list-container {
    grid-column: 1;
    grid-row: 2 / 4;
  }

  .request-container {
    grid-row: 2;
    grid-column: 2;
  }

  .response-container {
    grid-row: 3;
    grid-column: 2;
  }
}
